@import "main";

.two-columns {
    display: flex;
    flex-wrap: wrap;
}

.column {
    flex: 1;
    padding: 10px;
    box-sizing: border-box;
}

.column img {
    max-width: 100%;
    height: auto;
}

.large-heading {
    font-size: 3em; 
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .post-date {
    font-size: 0.4em; 
    float: right;
    font-weight: lighter;
    margin-left: 10px; 
  }